import {
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getAccountData } from "../../../redux/reducers/account";
import { getOrderBonusData } from "../../../redux/reducers/ordering";

const Bonus = () => {
  const dispatch = useDispatch();

  const { account } = useSelector(({ account }) => account);
  const { orderBonus } = useSelector(({ ordering }) => ordering);

  const [showMore, setShowMore] = useState(false);
  const ordersLength = orderBonus.length;

  const handleShowMore = () => {
    setShowMore(!showMore);
  };
  useEffect(() => {
    dispatch(getAccountData());
    dispatch(getOrderBonusData());
  }, []);

  ordersLength &&
    orderBonus?.sort((a, b) => {
      return a.id - b.id;
    });
  return (
    <>
      <Typography
        variant="h1"
        sx={{
          fontSize: { sm: "30px", xs: "22px" },
          lineHeight: "60px",
          fontWeight: "600",
          mb: { md: "30px", xs: "39px" },
        }}
      >
        Бонусная карта
      </Typography>
      <Typography
        sx={{
          fontSize: { sm: "16px", xs: "14px" },
          fontWeight: "500",
          lineHeight: { sm: "20.8px", xs: "18.2px" },
          mb: "22px",
        }}
      >
        Возвращаем 5% кэшбэком <br />
        Возвратим процент от суммы заказа на Ваш бонусный счет. <br />
        {/* Накопленные баллы у вас на счете. */}
        Проверьте свои баллы, если не поступили в течение 24 часов обратитесь в
        службу поддержки.
      </Typography>
      <Typography
        sx={{
          fontSize: { sm: "16px", xs: "14px" },
          fontWeight: "500",
          maxWidth: "670px",
          lineHeight: { sm: "20.8px", xs: "18.2px" },
          mb: "28px",
        }}
      >
        Оплати накопленными баллами! <br />
        Все легко и просто: <br />
        1 балл = 1 сом <br />
        Платите баллами отправку посылок. <br />
        Сэкономьте до 100 % с помощью накопленных баллов.
      </Typography>
      <Box
        sx={{
          borderRadius: "10px",
          p: { sm: "23px 30px", xs: "15px 13px" },
          width: { sm: "248px", xs: "169px" },
          height: { sm: "110px", xs: "64px" },
          mb: "40px",
        }}
        className="bonus-bg"
      >
        <Typography
          sx={{
            fontWeight: "500",
            fontSize: { sm: "16px", xs: "14px" },
            color: "#000",
            mb: { sm: "12px", xs: "4px" },
          }}
        >
          Накопленные бонусы
        </Typography>
        <Typography
          sx={{
            fontWeight: "700",
            fontSize: { sm: "40px", xs: "20px" },
            color: "#000",
          }}
        >
          {account && account.points} сом
        </Typography>
      </Box>
      {orderBonus.length > 0 && (
        <TableContainer component={Paper} sx={{ borderRadius: "20px" }}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell
                  sx={{
                    fontSize: "18px",
                    color: "#000",
                    p: { lg: "33px 16px 20px 65px", xs: "20px" },
                  }}
                >
                  Номер груза
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "18px",
                    color: "#000",
                    p: { lg: "33px 16px 20px", xs: "20px" },
                  }}
                >
                  Дата
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "18px",
                    color: "#000",
                    p: { lg: "33px 16px 20px", xs: "20px" },
                  }}
                >
                  Время
                </TableCell>
                <TableCell
                  sx={{
                    fontSize: "18px",
                    color: "#000",
                    p: { lg: "33px 16px 20px", xs: "20px" },
                  }}
                >
                  Сумма
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {orderBonus.length > 0 &&
                (orderBonus.length > 8
                  ? orderBonus
                      .reverse()
                      .slice(0, !showMore ? 8 : orderBonus.length)
                      .map((order) => (
                        <TableRow
                          key={order.id}
                          sx={{
                            "&:last-child td, &:last-child th": { border: 0 },
                          }}
                        >
                          <TableCell
                            component="th"
                            scope="row"
                            sx={{
                              border: "none",
                              width: { xs: "26%" },
                              p: { lg: "16px 16px 16px 65px", xs: "20px" },
                            }}
                          >
                            <Box sx={{ display: "flex", alignItems: "center" }}>
                              {/* <i
                      className={`ri-${row.type}-line`}
                      style={{
                        color: "#636363",
                        fontSize: "16px",
                        background: "#FFE5BF",
                        borderRadius: "9px",
                        display: "inline-flex",
                        width: "26px",
                        height: "26px",
                        alignItems: "center",
                        justifyContent: "center",
                      }}
                    /> */}
                              <Box
                                sx={{
                                  padding: "5px",
                                  background: "#FFE5BF",
                                  borderRadius: "9px",
                                  display: "inline-flex",
                                  width: "26px",
                                  height: "26px",
                                  alignItems: "center",
                                  justifyContent: "center",
                                }}
                              >
                                <img src={order?.icon} alt="" />
                              </Box>

                              <Box
                                sx={{ marginLeft: { md: "25px", xs: "15px" } }}
                              >
                                {order?.code}
                              </Box>
                            </Box>
                          </TableCell>
                          <TableCell
                            sx={{
                              border: "none",
                              width: { md: "18%", xs: "11%" },
                              p: { lg: "16px", xs: "20px" },
                            }}
                          >
                            {new Date(order?.sending_date).toLocaleDateString()}
                          </TableCell>
                          <TableCell
                            sx={{
                              border: "none",
                              width: { md: "19%", xs: "16%" },
                              p: { lg: "16px", xs: "20px" },
                            }}
                          >
                            {new Date(order?.sending_date).toLocaleTimeString(
                              [],
                              {
                                timeStyle: "short",
                              }
                            )}
                          </TableCell>
                          <TableCell
                            sx={{
                              border: "none",
                              width: { xs: "29%" },
                              p: { lg: "16px", xs: "20px" },
                            }}
                          >
                            <Box
                              sx={{
                                display: "flex",
                                justifyContent: "space-between",
                                maxWidth: "200px",
                              }}
                            >
                              <span
                                style={{
                                  background: "#BDEBB9",
                                  lineHeight: 1,
                                  padding: "5px",
                                  borderRadius: "8px",
                                }}
                              >
                                + {Math.trunc(order?.sum)}
                              </span>
                              {order?.parcel_sum && (
                                <span
                                  style={{
                                    background: "#FFD6D6",
                                    lineHeight: 1,
                                    padding: "5px",
                                    borderRadius: "8px",
                                  }}
                                >
                                  {` - ${Math.trunc(order?.parcel_sum)}`}
                                </span>
                              )}

                              <NavLink
                                to={`/dashboard/order-info/${order.code}`}
                              >
                                <i
                                  className="ri-share-box-fill"
                                  style={{ color: "#000", fontSize: "20px" }}
                                />
                              </NavLink>
                            </Box>
                          </TableCell>
                        </TableRow>
                      ))
                  : orderBonus.reverse().map((order) => (
                      <TableRow
                        key={order.id}
                        sx={{
                          "&:last-child td, &:last-child th": { border: 0 },
                        }}
                      >
                        <TableCell
                          component="th"
                          scope="row"
                          sx={{
                            border: "none",
                            width: { xs: "26%" },
                            p: { lg: "16px 16px 16px 65px", xs: "20px" },
                          }}
                        >
                          <Box sx={{ display: "flex", alignItems: "center" }}>
                            <Box
                              sx={{
                                padding: "5px",
                                background: "#FFE5BF",
                                borderRadius: "9px",
                                display: "inline-flex",
                                width: "26px",
                                height: "26px",
                                alignItems: "center",
                                justifyContent: "center",
                              }}
                            >
                              <img src={order?.icon} alt="" />
                            </Box>
                            <Box
                              sx={{ marginLeft: { md: "25px", xs: "15px" } }}
                            >
                              {order.code}
                            </Box>
                          </Box>
                        </TableCell>
                        <TableCell
                          sx={{
                            border: "none",
                            width: { md: "18%", xs: "11%" },
                            p: { lg: "16px", xs: "20px" },
                          }}
                        >
                          {new Date(order?.sending_date).toLocaleDateString()}
                        </TableCell>
                        <TableCell
                          sx={{
                            border: "none",
                            width: { md: "19%", xs: "16%" },
                            p: { lg: "16px", xs: "20px" },
                          }}
                        >
                          {new Date(order?.sending_date).toLocaleTimeString(
                            [],
                            {
                              timeStyle: "short",
                            }
                          )}
                        </TableCell>
                        <TableCell
                          sx={{
                            border: "none",
                            width: { xs: "29%" },
                            p: { lg: "16px", xs: "20px" },
                          }}
                        >
                          <Box
                            sx={{
                              display: "flex",
                              justifyContent: "space-between",
                              maxWidth: "200px",
                            }}
                          >
                            <span
                              style={{
                                background: "#BDEBB9",
                                lineHeight: 1,
                                padding: "5px",
                                borderRadius: "8px",
                              }}
                            >
                              + {Math.trunc(order?.sum)}
                            </span>
                            {order?.parcel_sum && (
                              <span
                                style={{
                                  background: "#FFD6D6",
                                  lineHeight: 1,
                                  padding: "5px",
                                  borderRadius: "8px",
                                }}
                              >
                                {` - ${Math.trunc(order?.parcel_sum)}`}
                              </span>
                            )}
                            <NavLink to={`/dashboard/order-info/${order.code}`}>
                              <i
                                className="ri-share-box-fill"
                                style={{ color: "#000", fontSize: "20px" }}
                              />
                            </NavLink>
                          </Box>
                        </TableCell>
                      </TableRow>
                    )))}
            </TableBody>
          </Table>
          <Box sx={{ textAlign: "center" }}>
            {ordersLength > 8 && (
              <Button
                variant="text"
                endIcon={
                  <i
                    className="ri-arrow-down-s-line"
                    style={{
                      fontSize: "24px",
                      marginLeft: "15px",
                      display: "inline-block",
                    }}
                  />
                }
                onClick={handleShowMore}
                sx={{
                  fontSize: { xs: "16px" },
                  fontWeight: "400",
                  m: "15px 0 38px",
                  textTransform: "unset",
                  color: "#000",
                }}
              >
                <span style={{ textDecoration: "underline" }}>
                  Смотреть ещё
                </span>
              </Button>
            )}
          </Box>
        </TableContainer>
      )}
      {orderBonus.length < 1 && (
        <Box sx={{ maxWidth: "320px" }}>
          <Typography
            sx={{
              fontWeight: "normal",
              fontSize: "20px",
              lineHeight: "46px",

              mb: "15px",
            }}
          >
            Вы ещё не отправляли груз
          </Typography>
          <Button
            component={NavLink}
            to="/dashboard/home"
            variant="contained"
            sx={{
              mt: { xxl: 5, xs: "20px" },
              width: "100%",
            }}
            size="large"
            color="primary"
          >
            отправить первый груз
          </Button>
        </Box>
      )}
    </>
  );
};

export default Bonus;
